  const [kpopmusicplus,kpopmusicplusEdit] = useState(data.filter(val=> val.type.toLowerCase().includes('k-pop')).slice(4,undefined));
  const [popmusic,setpopmusic] = useState(data.filter(val=> val.type.toLowerCase().includes('팝')).slice(0,4));
  const [popmusicplus,setpopmusicplus] = useState(data.filter(val=> val.type.toLowerCase().includes('팝')).slice(4,undefined));
  const [jmusic,setjmusic] = useState(data.filter(val=> val.type.toLowerCase().includes('j')).slice(0,4));
  const [jmusicplus,setjmusicplus] = useState(data.filter(val=> val.type.toLowerCase().includes('j')).slice(4,undefined));
  const [etcmusic,setetcmusic] = useState(data.filter(val=> val.type.toLowerCase().includes('연주곡')).slice(0,4));
  const [etcmusicplus,setetcmusicplus] = useState(data.filter(val=> val.type.toLowerCase().includes('연주곡')).slice(4,undefined));
  const [happymusic,sethappymusic] = useState(data.filter(val=> val.feel.toLowerCase().includes('신나는')).slice(0,4));
  const [happymusicplus,sethappymusicplus] = useState(data.filter(val=> val.feel.toLowerCase().includes('신나는')).slice(4,undefined));
  const [emotionmusic,setemotionmusic] = useState(data.filter(val=> val.feel.toLowerCase().includes('감각적인')).slice(0,4));
  const [emotionmusicplus,setemotionmusicplus] = useState(data.filter(val=> val.feel.toLowerCase().includes('감각적인')).slice(4,undefined));
  const [sadmusic,setsadmusic] = useState(data.filter(val=> val.feel.toLowerCase().includes('애절한')).slice(0,4));
