//컴포넌트

class MyComponent extends React.Component {
  constructor(props) {
    super(props)
    this.vantaRef = React.createRef()
  }
  componentDidMount() {
    this.vantaEffect = HALO({
      el: this.vantaRef.current,
  mouseControls: true,
  touchControls: true,
  gyroControls: false,
  minHeight: 200.00,
  minWidth: 200.00,
  baseColor: 0x0,
  backgroundColor: 0x22173a,
  amplitudeFactor: 1.10,
  size: 2
    })
  }
  componentWillUnmount() {
    if (this.vantaEffect) this.vantaEffect.destroy()
  }
  render() {
    return<div className='mainVisual' ref={this.vantaRef}>
      <div className='mainVisual_text'>
        <h1 className='mainVisual_logo' style={{fontSize:"0"}}>Wimp</h1>
        <ReactTypingEffect
        text={["오늘도 노동요를 찾아 헤매는 당신을 위해 -", "What's in my playlist !"]}
        cursorRenderer={cursor => <h1 style={{display:"none"}}>{cursor}</h1>}
        displayTextRenderer={(text, i) => {
          return (
            <h1 className='typing'>
              {text.split('').map((char, i) => {
                const key = `${i}`;
                return (
                  <span
                    key={key}
                  >{char}</span>
                );
              })}
            </h1>
          );
        }}
        speed= {50}
        eraseSpeed={30}
        eraseDelay={1500}
        typingDelay={1000}
      />
      </div>
      <div className='toBottom'>
      <img src='img/expand_more.svg'></img>
      <img src='img/expand_more.svg'></img>
      </div>
    </div>
  }
}

class MySlide extends React.Component {
  constructor(props) {
    super(props)
    this.vantaRef = React.createRef()
  }
  componentDidMount() {
    this.vantaEffect = TRUNK({
      el: this.vantaRef.current,
      mouseControls: true,
  touchControls: true,
  gyroControls: false,
  minHeight: 200.00,
  minWidth: 200.00,
  scale: 1.00,
  scaleMobile: 1.00,
  spacing: 13.00,
  chaos: 3.00,
  color: 0xb92282,
  backgroundColor: 0x22173a,
    p5: p5 
    })
  }
  componentWillUnmount() {
    if (this.vantaEffect) this.vantaEffect.destroy()
  }
  render() {
    return <div className='mainSlide' ref={this.vantaRef}>
      <Slide></Slide>
  </div>
  }
}


function Card(props){

  let history = useHistory();
  
  return(
    <div className="col-md-3 section00">
      <a onClick={()=>{history.push('/detail/'+(props.mainmusic.id)+'')}}><img src={'img/m'+(props.mainmusic.id+1)+'.jpg'} className="album" alt={props.mainmusic.title}/>
      <article>
          <h4>{ props.mainmusic.title }</h4>
          <p>{ props.mainmusic.con }</p>
      </article>
      </a>
    </div>
  )
}

function Tab01Dropdown(props) {
  const [key, setKey] = useState('K-POP');
  let history = useHistory();
  let [kcount,setkCount] = useState(0);
  let [jcount,setjCount] = useState(0);
  let [etccount,setetcCount] = useState(0);
  let [count,setCount] = useState(0);
  let count01Up = () => {setkCount(prevCount => prevCount + 1);}
  let count01Down = () => {setkCount(prevCount => prevCount - 1);}
  let count02Up = () => {setCount(prevCount => prevCount + 1);}
  let count02Down = () => {setCount(prevCount => prevCount - 1);}
  let count03Up = () => {setjCount(prevCount => prevCount + 1);}
  let count03Down = () => {setjCount(prevCount => prevCount - 1);}
  let count04Up = () => {setetcCount(prevCount => prevCount + 1);}
  let count04Down = () => {setetcCount(prevCount => prevCount - 1);}


  return (
    <Tabs
      id="controlled-tab-example"
      activeKey={key}
      onSelect={(k) => setKey(k)}
      className="mb-3"
      variant="pills"
    >
      <Tab eventKey="K-POP" title="K-POP">
      <div className="row">
         {
         props.kpopmusic.map((a,i)=>{
          return <div className="col-md-3">
          <a onClick={()=>{history.push('/detail/'+(a.id)+'')}}><img src={'img/m'+(a.id+1)+'.jpg'} className="album" alt={a.title}/>
          <article>
              <h4>{ a.title }</h4>
              <p>{ a.con }</p>
          </article>
          </a>
          </div>
         })
        }
        {
          kcount < 1
          ? <button className='btn' onClick={()=>{ props.kpopmusicEdit([...props.kpopmusic,...props.kpopmusicplus]);count01Up();} }>더보기</button>
          : <button className='btn' onClick={()=>{ props.kpopmusicEdit([...props.kpopmusic.slice(0,4)]);count01Down();} }>닫기</button>
          }

        </div>
      </Tab>

      <Tab eventKey="POP" title="POP">
         <div className="row">
         {
         props.popmusic.map((a,i)=>{
          return <div className="col-md-3">
          <a onClick={()=>{history.push('/detail/'+(a.id)+'')}}><img src={'img/m'+(a.id+1)+'.jpg'} className="album" alt={a.title}/>
          <article>
              <h4>{ a.title }</h4>
              <p>{ a.con }</p>
          </article>
          </a>
          </div>
         })
        }
        {
          count < 1
          ? <button className='btn' onClick={()=>{ props.setpopmusic([...props.popmusic,...props.popmusicplus]);count02Up();} }>더보기</button>
          : <button className='btn' onClick={()=>{ props.setpopmusic([...props.popmusic.slice(0,4)]);count02Down();} }>닫기</button>
          }
        </div>
      </Tab>

      <Tab eventKey="J-POP" title="J-POP">
         <div className="row">
         {
         props.jmusic.map((a,i)=>{
          return <div className="col-md-3">
          <a onClick={()=>{history.push('/detail/'+(a.id)+'')}}><img src={'img/m'+(a.id+1)+'.jpg'} className="album" alt={a.title}/>
          <article>
              <h4>{ a.title }</h4>
              <p>{ a.con }</p>
          </article>
          </a>
          </div>
         })
        }
        {
          jcount < 1
          ? <button className='btn' onClick={()=>{ props.setjmusic([...props.jmusic,...props.jmusicplus]);count03Up();} }>더보기</button>
          : <button className='btn' onClick={()=>{ props.setjmusic([...props.jmusic.slice(0,4)]);count03Down();} }>닫기</button>
          }
        </div>
      </Tab>

      <Tab eventKey="etc" title="연주곡">
        <div className="row">
        {
         props.etcmusic.map((a,i)=>{
          return <div className="col-md-3">
          <a onClick={()=>{history.push('/detail/'+(a.id)+'')}}><img src={'img/m'+(a.id+1)+'.jpg'} className="album" alt={a.title}/>
          <article>
              <h4>{ a.title }</h4>
              <p>{ a.con }</p>
          </article>
          </a>
          </div>
         })
        }
        {
          etccount < 1
          ? <button className='btn' onClick={()=>{ props.setetcmusic([...props.etcmusic,...props.etcmusicplus]);count04Up();} }>더보기</button>
          : <button className='btn' onClick={()=>{ props.setetcmusic([...props.etcmusic.slice(0,4)]);count04Down();} }>닫기</button>
          }
          </div>
      </Tab>

    </Tabs>
  );
}

