function Card02(props){
  let history = useHistory();
  const getRandomIndex = function(length){
    return parseInt(Math.random() * length)
  }
  let [randomNum,randomNumEdit] = useState(getRandomIndex(props.music.length));

  return(
    <div className="col-md-3">
      <a onClick={()=>{history.push('/detail/'+(props.music[randomNum].id)+'')}}><img src={'img/m'+(props.music[randomNum].id+1)+'.jpg'} className="album card02" alt={props.music[randomNum].title}/>
      <hr className="line"></hr>
          <h4>{ props.music[randomNum].title }</h4>
          <p>{ props.music[randomNum].con }</p>
      </a>
    </div>
  )
}

