import React, {useState} from 'react';
import data from './data.js';
import { useHistory, useParams } from 'react-router-dom';
import DOTS from 'vanta/dist/vanta.dots.min.js';

function Search(){
    let history = useHistory();
    let [searchKey, setsearchKey] = useState('')
    let [searchTerm, setSearchTerm] = useState('')

    const onClick = () => {setSearchTerm(searchKey)};

  return (
  <div className='search_div'>
    <SearchBox></SearchBox>
    <div className='search_box'>
    <p>음원명, 가수명, 분위기(신나는, 감각적인, 애절한, 드라마틱) 등으로 해당하는 음원을 검색할 수 있습니다.</p>
    <input type="text" placeholder="검색어 입력" onChange={event => {setsearchKey(event.target.value)}} onKeyPress={(e)=>{if(e.key === 'Enter'){onClick();}}}/>
    <button onClick={() => {setSearchTerm(searchKey)}}>검색</button>
    </div>

    <div className="container01">
    <div className="row">
    {data.filter((val)=> {
      if (searchTerm == "")
      {return val}
      else if (val.feel.toLowerCase().includes(searchTerm.toLowerCase()) || val.title.toLowerCase().includes(searchTerm.toLowerCase()) || val.con.toLowerCase().includes(searchTerm.toLowerCase()))
      {return val}
    }).map((val, key)=> {
      return <div className="col-md-3" key={key}>
        <a onClick={()=>{history.push('/detail/'+(val.id)+'')}}><img src={'img/m'+(val.id+1)+'.jpg'} className="album search" alt={'music'+val.title}/></a>
          <h4 className="white">{ val.title }</h4>
          <p className="white">{ val.con }</p>
        </div>
    })}
    </div>
    </div>
  </div>
  );
}

class SearchBox extends React.Component {
  constructor(props) {
    super(props)
    this.vantaRef = React.createRef()
  }
  componentDidMount() {
    this.vantaEffect = DOTS({
      el: this.vantaRef.current,
      mouseControls: true,
  touchControls: true,
  gyroControls: false,
  minHeight: 200.00,
  minWidth: 200.00,
  scale: 1.00,
  scaleMobile: 1.00,
  color: 0xfc2ea2,
  color2: 0xfc2ea2,
  backgroundColor: 0x2f2645
    })
  }
  componentWillUnmount() {
    if (this.vantaEffect) this.vantaEffect.destroy()
  }
  render() {
    return<div className='search_back' ref={this.vantaRef}>
    </div>
  }
}

  export default Search;
